// ******************** code comments ********************
// tab
// ******************** code comments ********************
/*!    -   */
.tab-nav-container{
    //   
    &.scroll{
        overflow-y: hidden;
        overflow-x: auto;
        white-space: nowrap;
        margin:0 auto;
        -webkit-overflow-scrolling: touch;
        // @include rem(width, );
        @include rem(width, 300px);

        // scroll    ()
        &::-webkit-scrollbar {
            height: 8px;
        }
        &::-webkit-scrollbar-thumb {
            background-color: rgb(134, 133, 133);
            border-radius: 10px;
            background-clip: padding-box;
            border: 1px solid transparent;
        }
        &::-webkit-scrollbar-track {
            background-color: red;
            border-radius: 10px;
            box-shadow: inset 0 0 3px white;
        }
    }
}

/*!    -   -   */
.tab-nav{
    width:100%;
    display:table;
    table-layout: fixed;

    .tab-item{
        display:table-cell;
        @include rem(height, 70px);
        @include rem(padding, 0 1px);
        &:first-child{
            padding-left:0;
        }
        &:last-child{
            padding-right:0;
        }
    }
    .tab-link{
        display:table;
        width:100%;
        height:100%;
        text-align:center;
        background-color: gray;
        transition:background-color .2s ease-out;
        .hide-txt{display:none;}
        .txt{
            //   ...      
            display:table-cell;
            @include rem(padding, 10px);
            @include rem(font-size, 20px);
            color:lightgray;
        }

        &:hover,
        &:focus,
        &:active,
        &.active{
            background-color: blue;
            .txt{
                // font-weight:500;
                color:#ffffff;
            }
        }
        &.active{
            .hide-txt{display:inline-block;}
        }
    }

    //       
    &.unequal{
        display:inline-table;
        table-layout: auto;
    }
}

/*!    -    */
.tab-content{
    .tab-pane{
        display:none;
        &.active{
            display:block;
        }
    }
}
